
.nav {
  width: 100%;
  height: 50px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #fff;
}
.nav .nav {
  width: 33%;
  height: 100%;
}
.nav .nav a {
  height: 100%;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-decoration: none;
  color: #666;
  font-size: 18px;
  text-align: center;
}
.nav .nav a span:first-child {
  height: 30px;
  width: 100%;
  line-height: 30px;
}
.nav div:nth-child(2) > a > span {
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
}
.nav .active {
  border-bottom: 2px solid #f00;
}


