
.menu {
  position: fixed;
  top: 0;
  right: 0;
  color: #fff;
  width: 120px;
  height: 100%;
  background: #3c3c3d;
  z-index: 9;
}
.menu .user {
  height: 60px;
  line-height: 60px;
  background: #000;
  width: 60%;
  padding: 0 20%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.menu .aside li {
  height: 60px;
  line-height: 60px;
  width: 60%;
  padding: 0 20%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}


