
.select {
  background: #fff;
}
.select .banner {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
}
.select .banner .box {
  height: 200px;
  width: 400%;
  position: absolute;
  left: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.select .banner .img {
  height: 200px;
  width: 100%;
  -webkit-transition: all 1s;
  transition: all 1s;
}
.select .banner .num {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.select .banner .num .active {
  background: #058 !important;
}
.select .banner .num li {
  height: 10px;
  width: 10px;
  cursor: pointer;
  background: #fff;
  border-radius: 50%;
  margin: 10px;
}
.select .news {
  padding: 0 10px;
}
.select .news .new {
  height: 100px;
  color: #262627;
  border-bottom: 1px solid #eee;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
}
.select .news .new img {
  height: 80px;
  width: 100px;
}
.select .news .new .intro {
  width: 80%;
  height: 80px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 10px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.select .news .new .intro h4 {
  font-size: 20px;
  line-height: 1.2;
  font-weight: bold;
  overflow: hidden;
  display: -webkit-box;
  word-break: break-all;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
}
.select .news .new .intro p {
  font-size: 13px;
  color: #666;
}


