
.head {
  height: 60px;
  background: #262627;
  position: relative;
}
.head img {
  height: 50px;
  position: absolute;
  top: 5px;
}
.head .baijia {
  height: 40px;
  top: 10px;
  left: 10px;
}
.head .baidu {
  right: 30%;
}
.head .searchBtn {
  right: 15%;
  cursor: pointer;
}
.head .aside {
  height: 60px;
  width: 60px;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  padding-top: 1px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
}
.head .aside .line {
  height: 1px;
  width: 25px;
  background: #fff;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.head .aside .line:nth-of-type(2) {
  margin: 6px 0;
}
.head .closeMenu .line:first-child {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.head .closeMenu .line:nth-child(2) {
  display: none;
}
.head .closeMenu .line:last-child {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.head .searchPage {
  display: none;
  position: fixed;
  height: 100%;
  width: 100%;
  background: #fff;
  z-index: 999;
}
.head .searchPage .header {
  height: 60px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 2%;
  background: #262627;
}
.head .searchPage .header .search {
  height: 40px;
  width: 82%;
  border: 1px solid #eee;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.head .searchPage .header .close {
  height: 40px;
  width: 12%;
  margin-left: 2%;
  padding-top: 34px;
}
.head .searchPage .header .close .closeLine {
  height: 1px;
  width: 70%;
  background: #fff;
}
.head .searchPage .header .close .closeLine:first-child {
  color: #f00;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.head .searchPage .header .close .closeLine:last-child {
  color: #f00;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}


