
.author {
  background: #fff;
  border-right: 1px solid #666;
}
.author .banner {
  background: url("../assets/author/author_banner_bg.jpg");
}
.author .banner .item {
  height: 210px;
  width: 45%;
  margin: 2%;
  color: #fff;
  opacity: 0.9;
  display: inline-block;
  text-align: center;
  background: #2e2e2f;
}
.author .banner .item .msg {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #262627;
  padding: 10px 0;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.author .banner .item .msg img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
}
.author .banner .item .msg .name {
  margin: 10px 0;
}
.author .banner .item .msg .slogan {
  color: #666;
  width: 80%;
  font-size: 13px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.author .banner .item .focus {
  font-size: 13px;
  display: inline-block;
  padding: 4px 20px;
  background: #e94c3d;
  border-radius: 10px;
  cursor: pointer;
  margin: 10px 0;
}
.author .banner .item .focused {
  background: #262627;
}
.author .more {
  padding: 10px;
}
.author .more .other {
  height: 100px;
  background: #eee;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 10px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  padding: 0 10px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.author .more .other img {
  height: 60px;
  width: 60px;
  border-radius: 50%;
}
.author .more .other .intro {
  height: 40px;
  margin: 0 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.author .more .other .intro .slogan {
  font-size: 13px;
  color: #666;
}
.author .more .other .focus {
  color: #fff;
  cursor: pointer;
  font-size: 13px;
  text-align: center;
  padding: 5px 15px;
  background: #d5483a;
  border-radius: 15px;
  margin-left: 10px;
}
.author .more .other .focused {
  background: #262627;
}


